.stage {
	position: relative;
	width: 180px;
	margin: 0 auto;
	padding: 0;

	@media (min-width: $screen-lg-min) {
		width: 250px;
	}

	&__back {
		width: 100%;
	}

	&__front {
		position: absolute;
		left: 0;
		bottom: 0.5%;
		width: 100%;
	}

	&__hole {
		position: absolute;
		bottom: 32%;
		left: 0;
		right: 0;
		width: 100%;
		margin: 0 auto;

		@media (min-width: $screen-lg-min) {
			bottom: 34.3%;
		}

		svg {
			margin: 0 auto;
			width: 65%;
		}
	}

	&__bottle {
		position: absolute;
		bottom: 45%;
		left: 0;
		right: 0;
		margin: 0 auto;
		width: 25%;
		transition: 0.6s ease;

		&--moved {
			transform: translateY(1000px);
		}
	}

	&__glass {
		position: relative;
		width: 100%;
		margin-bottom: -28%;
		z-index: 10;
	}

	.cover {
		transition: 0.4s ease;

		&__shadow {
			transition: 0.4s ease;
		}
	}
}