// Box sizing partial styles:
// Apply a natural box layout model to all elements
*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

html {
	font-size: 12px;

	@media (min-width: $screen-lg-min) {
		font-size: 14px;
	}
}

body {
	font-size: 1rem;
}