.advantage {
	margin: 20px 0;
	font-family: $font-main;
	font-display: swap;
	font-size: 1rem;

	&--narrow {
		margin-left: 30px;
		margin-right: 30px;
	}

	&__img {
		display: block;
		width: 100%;
		margin: 0 auto;

		&--small {
		    width: 40%;
		}
	}

	&__text {
		color: #fff;
		text-align: center;
	}
}