.navbar {
	position: fixed;
	z-index: 10;
	right: 40px;
	top: 50%;
	transform: translateY(-50%);
	display: none;

	@media (min-width: $screen-lg-min) {
		display: block;
	}
	
	&__list {
		margin: 0;
		padding: 0;
		list-style: none;
	}

	&__item {
		display: block;
		margin: 20px 0;

		&--invisible-lg {
			@media (min-width: $screen-lg-min) {
				display: none;
			}
		}

		#Group-Copy > g {
			fill: #9e9e9e;
			transition: 0.4s ease;
		}

		&.active #Group-Copy > g {
			fill: inherit;
		}
	}
}